	module.exports = {"data":{"site":{"siteMetadata":{"title":"Justin Ball","author":"Justin Ball"}},"markdownRemark":{"id":"/Users/jbasdf/projects/justinball/src/posts/2008-03-12-family-tree-read/index.md absPath of file >>> MarkdownRemark","html":"<p>What do we read out of family search?</p>\n<ul>\n<li>Person Information</li>\n<li>Search Match</li>\n<li>Authorities Data - places</li>\n<li>LDS Ordinance Information</li>\n</ul>\n<p>Objects available in the new FamilySearch</p>\n<ul>\n<li>Person</li>\n<li>Search Data</li>\n<li>Contributor Information</li>\n<li>Place Standardization Data</li>\n</ul>\n<p>Can output XML, JSON, and XML-FastInfoset. Request can be gzipped if client supported.</p>\n<p>Locate a resource via NFS Id - 'KWCD-QBC', Ancestral File Number - 'afn.2bW3-9H' or Gedcom UID.</p>\n<p>Examples:</p>\n<p><a href=\"https://api.familysearch.org/familytree/v1/person/KWCD-QBC?sessionId=MMMM\">https://api.familysearch.org/familytree/v1/person/KWCD-QBC?sessionId=MMMM</a></p>\n<p><a href=\"https://api.familysearch.org/authorities/v1/place/933423?sessionId=MMMM\">https://api.familysearch.org/authorities/v1/place/933423?sessionId=MMMM</a></p>\n<p><a href=\"https://api.familysearch.org/v1/search?name=Isreal\">https://api.familysearch.org/v1/search?name=Isreal</a> Hoyt heaton?sessionId=MMMM</p>\n<p>Get the schema:</p>","frontmatter":{"title":"Family Tree Read","date":"March 12, 2008","imageUrl":null,"structuredHtml":null,"image":null}}},"pathContext":{}}

